<template>
  <b-row>
    <b-col
      cols="12"
      class="mb-2"
    >
      <h5 class="mb-0">
        {{ $t('attachments.title') }}
      </h5>
      <small class="text-muted">
        {{ $t('attachments.description') }}
      </small>
    </b-col>
    <b-col md="4">
      <div id="app">
        <file-pond
          ref="husbandFront"
          required="true"
          name="data"
          :label-idle="$t('attachments.husbandFront')"
          :allow-multiple="false"
          accepted-file-types="image/jpeg, image/png"
          :server="server"
          @processfile="husbandFrontEvent"
          @removefile="$emit('update:husbandFront', null)"
        />
      </div></b-col>
    <b-col md="4">
      <file-pond
        ref="husbandBack"
        required="true"
        name="data"
        :label-idle="$t('attachments.husbandBack')"
        :allow-multiple="false"
        accepted-file-types="image/jpeg, image/png"
        :server="server"
        @processfile="husbandBackEvent"
        @removefile="$emit('update:husbandBack', null)"
      />
    </b-col>
    <b-col md="4">
      <file-pond
        ref="wifeFront"
        required="true"
        name="data"
        :label-idle="$t('attachments.wifeFront')"
        :allow-multiple="false"
        accepted-file-types="image/jpeg, image/png"
        :server="server"
        @processfile="wifeFrontEvent"
        @removefile="$emit('update:wifeFront', null)"
      />
    </b-col>
    <b-col md="4">
      <file-pond
        ref="wifeBack"
        required="true"
        name="data"
        :label-idle="$t('attachments.wifeBack')"
        :allow-multiple="false"
        accepted-file-types="image/jpeg, image/png"
        :server="server"
        @processfile="wifeBackEvent"
        @removefile="$emit('update:wifeBack', null)"
      />
    </b-col>
    <b-col md="4">
      <file-pond
        ref="firstWitnessFront"
        required="true"
        name="data"
        :label-idle="$t('attachments.firstWitnessFront')"
        :allow-multiple="false"
        accepted-file-types="image/jpeg, image/png"
        :server="server"
        @processfile="firstWitnessFrontEvent"
        @removefile="$emit('update:firstWitnessFront', null)"
      />
    </b-col>
    <b-col md="4">
      <file-pond
        ref="firstWitnessBack"
        required="true"
        name="data"
        :label-idle="$t('attachments.firstWitnessBack')"
        :allow-multiple="false"
        accepted-file-types="image/jpeg, image/png"
        :server="server"
        @processfile="firstWitnessBackEvent"
        @removefile="$emit('update:firstWitnessBack', null)"
      />
    </b-col>
    <b-col md="4">
      <file-pond
        ref="secondWitnessFront"
        required="true"
        name="data"
        :label-idle="$t('attachments.secondWitnessFront')"
        :allow-multiple="false"
        accepted-file-types="image/jpeg, image/png"
        :server="server"
        @processfile="secondWitnessFrontEvent"
        @removefile="$emit('update:secondWitnessFront', null)"
      />
    </b-col>
    <b-col md="4">
      <file-pond
        ref="secondWitnessBack"
        required="true"
        name="data"
        :label-idle="$t('attachments.secondWitnessBack')"
        :allow-multiple="false"
        accepted-file-types="image/jpeg, image/png"
        :server="server"
        @processfile="secondWitnessBackEvent"
        @removefile="$emit('update:secondWitnessBack', null)"
      />
    </b-col>
    <b-col md="4">
      <file-pond
        ref="agentFront"
        required="true"
        name="data"
        :label-idle="$t('attachments.agentFront')"
        :allow-multiple="false"
        accepted-file-types="image/jpeg, image/png"
        :server="server"
        @processfile="agentFrontEvent"
        @removefile="$emit('update:agentFront', null)"
      />
    </b-col>
    <b-col md="4">
      <file-pond
        ref="agentBack"
        required="true"
        name="data"
        :label-idle="$t('attachments.agentBack')"
        :allow-multiple="false"
        accepted-file-types="image/jpeg, image/png"
        :server="server"
        @processfile="agentBackEvent"
        @removefile="$emit('update:agentBack', null)"
      />
    </b-col>
    <b-col
      v-if="status === 'd'"
      md="4"
    >
      <file-pond
        ref="divorceCertificate"
        required="true"
        name="data"
        :label-idle="$t('attachments.divorceCertificate')"
        :allow-multiple="false"
        accepted-file-types="image/jpeg, image/png"
        :server="server"
        @processfile="divorceCertificateEvent"
        @removefile="$emit('update:divorceCertificate', null)"
      />
    </b-col>
    <b-col
      v-if="status === 'w'"
      md="4"
    >
      <file-pond
        ref="husbandDeathCertificate"
        required="true"
        name="data"
        :label-idle="$t('attachments.husbandDeathCertificate')"
        :allow-multiple="false"
        accepted-file-types="image/jpeg, image/png"
        :server="server"
        @processfile="husbandDeathCertificateEvent"
        @removefile="$emit('update:husbandDeathCertificate', null)"
      />
    </b-col>
  </b-row>
</template>

<script>
// Import Vue FilePond
import vueFilePond from 'vue-filepond'

// Import FilePond styles
import 'filepond/dist/filepond.min.css'
// Import FilePond plugins
// Please note that you need to install these plugins separately

// Import image preview plugin styles
import 'filepond-plugin-image-preview/dist/filepond-plugin-image-preview.min.css'

// Import image preview and file type validation plugins
import FilePondPluginFileValidateType from 'filepond-plugin-file-validate-type'
import FilePondPluginImagePreview from 'filepond-plugin-image-preview'

import { localize } from 'vee-validate'
// import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
// import 'vue-form-wizard/dist/vue-form-wizard.min.css'
// import '@core/scss/vue/libs/vue-wizard.scss'
import {
  BRow,
  BCol,
  // BFormGroup,
  // BFormInput,
  // BFormSelect,
  // BFormFile,
  // BCard,
} from 'bootstrap-vue'
import { required, email } from '@validations'
import { domain } from '@/libs/axios'

// Create component
const FilePond = vueFilePond(
  FilePondPluginFileValidateType,
  FilePondPluginImagePreview,
)
// import ar from 'vee-validate/dist/locale/ar.json'

// Install English and Arabic locales.
// localize({
//   dic,
// })
const dic = {
  ar: {
    names: {
      email: 'البريد الاليكتروني',
      phone: 'رقم الهاتف',
      Password: 'كلمة السر',
      website: 'الموقع الالكتروني',
      country: 'المحافظة',
      landline_number: 'الرقم المباشر',
      direct_employee_name: 'اسم الموظف المباشر',
      direct_employee_phone: 'رقم هاتف الموظف المباشر',
      factory_register: 'سجل المصنع',
      factory_register_image: 'صورة سجل المصنع',
      letter_image: 'صورة الخطاب',
      address: 'العنوان',
    },
  },
}
localize('ar', dic.ar)
export default {
  name: 'Attachments',
  components: {
    // ValidationProvider,
    // ValidationObserver,
    FilePond,
    BRow,
    BCol,
    // eslint-disable-next-line vue/no-unused-components
    // ToastificationContent,
  },
  props: {
    label: {
      type: String,
      default: null,
    },
    husbandFront: {
      type: String,
      default: '',
    },
    husbandBack: {
      type: String,
      default: null,
    },
    wifeFront: {
      type: String,
      default: null,
    },
    wifeBack: {
      type: String,
      default: null,
    },
    firstWitnessFront: {
      type: String,
      default: null,
    },
    firstWitnessBack: {
      type: String,
      default: null,
    },
    secondWitnessFront: {
      type: String,
      default: null,
    },
    secondWitnessBack: {
      type: String,
      default: null,
    },
    agentFront: {
      type: String,
      default: null,
    },
    agentBack: {
      type: String,
      default: null,
    },
    divorceCertificate: {
      type: String,
      default: null,
    },
    husbandDeathCertificate: {
      type: String,
      default: null,
    },
    type: {
      type: String,
      required: false,
      default: '',
    },
    status: {
      type: String,
      required: true,
      default: 's',
    },
  },
  data() {
    return {
      myFiles: ['cat.jpeg'],
      server: `${domain}upload`,
      locale: 'ar',
      required,
      email,
    }
  },
  watch: {
    husbandFront() {
      console.log(this.husbandFront)
    },
  },
  created() {},
  methods: {
    husbandFrontEvent(error, file) {
      const data = JSON.parse(file.serverId)
      this.$emit('update:husbandFront', data.data)
    },
    husbandBackEvent(error, file) {
      const data = JSON.parse(file.serverId)
      this.$emit('update:husbandBack', data.data)
    },
    wifeFrontEvent(error, file) {
      const data = JSON.parse(file.serverId)
      this.$emit('update:wifeFront', data.data)
    },
    wifeBackEvent(error, file) {
      const data = JSON.parse(file.serverId)
      this.$emit('update:wifeBack', data.data)
    },
    firstWitnessFrontEvent(error, file) {
      const data = JSON.parse(file.serverId)
      this.$emit('update:firstWitnessFront', data.data)
    },
    firstWitnessBackEvent(error, file) {
      const data = JSON.parse(file.serverId)
      this.$emit('update:firstWitnessBack', data.data)
    },
    secondWitnessFrontEvent(error, file) {
      const data = JSON.parse(file.serverId)
      this.$emit('update:secondWitnessFront', data.data)
    },
    secondWitnessBackEvent(error, file) {
      const data = JSON.parse(file.serverId)
      this.$emit('update:secondWitnessBack', data.data)
    },
    agentFrontEvent(error, file) {
      const data = JSON.parse(file.serverId)
      this.$emit('update:agentFront', data.data)
    },
    agentBackEvent(error, file) {
      const data = JSON.parse(file.serverId)
      this.$emit('update:agentBack', data.data)
    },
    divorceCertificateEvent(error, file) {
      const data = JSON.parse(file.serverId)
      this.$emit('update:divorceCertificate', data.data)
    },
    husbandDeathCertificateEvent(error, file) {
      const data = JSON.parse(file.serverId)
      this.$emit('update:husbandDeathCertificate', data.data)
    },
  },
}

</script>
<style lang="scss">
//@import '@core/scss/vue/libs/vue-select.scss';
.filepond--root {
  direction: rtl !important;
}
.filepond--root .filepond--credits[style] {
  display: none !important;
}
</style>
