<template>
  <div>
    <form-wizard
      color="#7367F0"
      :title="null"
      :subtitle="null"
      :finish-button-text="$t('pages.createContract.submit')"
      :next-button-text="$t('pages.createContract.next')"
      :back-button-text="$t('pages.createContract.back')"
      class="mb-3"
      :hide-buttons="false"
      @on-complete="formSubmitted"
    >
      <tab-content
        :title="$t('pages.createContract.contractData')"
        :before-change="validationFormDate"
      >
        <validation-observer
          ref="infoDateRules"
          tag="form"
        >
          <b-row>
            <b-col md="4">
              <b-form-group
                :label="$t('pages.createContract.dateOfContract')"
                label-for="quranDate"
              >
                <validation-provider
                  #default="{ errors }"
                  name="quranDate"
                  rules="required"
                >
                  <b-form-input
                    id="quranDate"
                    v-model="quranDate"
                    :min="getDate()"
                    :state="errors.length > 0 ? false : null"
                    type="date"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
            </b-col>
            <b-col md="6">
              <b-form-group
                :label="$t('pages.createContract.placeOfContract')"
                label-for="isMosque"
              >
                <validation-provider
                  #default="{ errors }"
                  name="isMosque"
                  rules="required"
                >
                  <b-form-radio-group
                    id="isMosque"
                    v-model="isMosque"
                    :options="options"
                    name="isMosque"
                    :state="errors.length > 0 ? false : null"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
            </b-col>
            <b-col
              v-if="!isMosque"
              md="12"
            >
              <b-form-group
                :label="$t('pages.createContract.contractTitle')"
                label-for="quranAddress"
              >
                <validation-provider
                  #default="{ errors }"
                  name="quranAddress"
                  rules="required"
                >
                  <b-form-textarea
                    id="quranAddress"
                    v-model="quranAddress"
                    placeholder="Enter address ..."
                    rows="3"
                    max-rows="6"
                    :state="errors.length > 0 ? false : null"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
            </b-col>
          </b-row>
        </validation-observer>
      </tab-content>

      <!-- contract details tab -->
      <tab-content
        :title="$t('pages.createContract.husband')"
        :before-change="validationForm"
      >
        <validation-observer
          ref="husbandRules"
          tag="form"
        >
          <husband
            :first-name.sync="husband.firstName"
            :middle-name.sync="husband.middleName"
            :last-name.sync="husband.lastName"
            :email-address.sync="husband.email"
            :birthdate.sync="husband.birthdate"
            :birthplace.sync="husband.birthplace"
            :marital.sync="husband.maritalStatus"
            :marital-option="maritalOptionHusband"
            :nationality.sync="husband.nationality"
            :profession.sync="husband.profession"
            :address.sync="husband.address"
            :phone.sync="husband.phone"
            :type.sync="husband.type"
          />
        </validation-observer>
      </tab-content>

      <!-- personal details tab -->
      <tab-content
        :title="$t('pages.createContract.wife')"
        :before-change="validationFormWife"
      >
        <validation-observer
          ref="wifeRules"
          tag="form"
        >
          <wife
            :first-name.sync="wife.firstName"
            :middle-name.sync="wife.middleName"
            :last-name.sync="wife.lastName"
            :email-address.sync="wife.email"
            :birthdate.sync="wife.birthdate"
            :birthplace.sync="wife.birthplace"
            :marital.sync="wife.maritalStatus"
            :marital-option="maritalOptionWife"
            :nationality.sync="wife.nationality"
            :profession.sync="wife.profession"
            :address.sync="wife.address"
            :phone.sync="wife.phone"
            :type.sync="wife.type"
          />
        </validation-observer>
      </tab-content>

      <!-- personal details tab -->
      <tab-content
        :title="$t('pages.createContract.firstWitness')"
        :before-change="validationFormFirstWitness"
      >
        <validation-observer
          ref="firstWitnessRules"
          tag="form"
        >
          <wife
            :first-name.sync="firstWitness.firstName"
            :middle-name.sync="firstWitness.middleName"
            :last-name.sync="firstWitness.lastName"
            :email-address.sync="firstWitness.email"
            :birthdate.sync="firstWitness.birthdate"
            :birthplace.sync="firstWitness.birthplace"
            :marital-status.sync="firstWitness.maritalStatus"
            :nationality.sync="firstWitness.nationality"
            :profession.sync="firstWitness.profession"
            :address.sync="firstWitness.address"
            :phone.sync="firstWitness.phone"
            :type.sync="firstWitness.type"
          />
        </validation-observer>
      </tab-content>

      <!-- personal details tab -->
      <tab-content
        :title="$t('pages.createContract.secondWitness')"
        :before-change="validationFormSecondWitness"
      >
        <validation-observer
          ref="secondWitnessRules"
          tag="form"
        >
          <wife
            :first-name.sync="secondWitness.firstName"
            :middle-name.sync="secondWitness.middleName"
            :last-name.sync="secondWitness.lastName"
            :email-address.sync="secondWitness.email"
            :birthdate.sync="secondWitness.birthdate"
            :birthplace.sync="secondWitness.birthplace"
            :marital-status.sync="secondWitness.maritalStatus"
            :nationality.sync="secondWitness.nationality"
            :profession.sync="secondWitness.profession"
            :address.sync="secondWitness.address"
            :phone.sync="secondWitness.phone"
            :type.sync="secondWitness.type"
          />
        </validation-observer>
      </tab-content>

      <!-- personal details tab -->
      <tab-content
        :title="$t('pages.createContract.agent')"
        :before-change="validationFormAgent"
      >
        <validation-observer
          ref="agentRules"
          tag="form"
        >
          <agent
            :first-name.sync="agent.firstName"
            :middle-name.sync="agent.middleName"
            :last-name.sync="agent.lastName"
            :email-address.sync="agent.email"
            :birthdate.sync="agent.birthdate"
            :birthplace.sync="agent.birthplace"
            :marital.sync="agent.maritalStatus"
            :nationality.sync="agent.nationality"
            :profession.sync="agent.profession"
            :address.sync="agent.address"
            :phone.sync="agent.phone"
            :type.sync="agent.type"
            :kinship-degree.sync="agent.kinshipDegree"
            :kinship-degree-option.sync="agent.kinshipDegreeOption"
          />
        </validation-observer>
      </tab-content>
      <!-- personal details tab -->

      <tab-content
        :title="$t('pages.createContract.attachments')"
      >
        <validation-observer
          ref="attachmentsRules"
          tag="form"
        >
          <attachments
            :husband-front.sync="husbandFront"
            :husband-back.sync="husbandBack"
            :wife-front.sync="wifeFront"
            :wife-back.sync="wifeBack"
            :first-witness-front.sync="firstWitnessFront"
            :first-witness-back.sync="firstWitnessBack"
            :second-witness-front.sync="secondWitnessFront"
            :second-witness-back.sync="secondWitnessBack"
            :agent-front.sync="agentFront"
            :agent-back.sync="agentBack"
            :divorce-certificate.sync="divorceCertificate"
            :husband-death-certificate.sync="husbandDeathCertificate"
            :status="statusFile(wife.maritalStatus)"
          />
        </validation-observer>
      </tab-content>

    </form-wizard>
  </div>
</template>

<script>
import { FormWizard, TabContent } from 'vue-form-wizard'
import { ValidationProvider, ValidationObserver, localize } from 'vee-validate'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import 'vue-form-wizard/dist/vue-form-wizard.min.css'
import '@core/scss/vue/libs/vue-wizard.scss'
import { required } from '@validations'
import Husband from '@/views/admin/contracts/Inputs/Husband.vue'
import Wife from '@/views/admin/contracts/Inputs/Wife.vue'
import Agent from '@/views/admin/contracts/Inputs/Agent.vue'
import Attachments from '@/views/admin/contracts/Inputs/Attachments.vue'

import {
  BRow,
  BCol,
  BFormGroup,
  BFormInput,
  BFormRadioGroup,
  BFormTextarea,
  // BFormSelect,
  // BCard,
} from 'bootstrap-vue'
// import ar from 'vee-validate/dist/locale/ar.json'

// Install English and Arabic locales.
// localize({
//   dic,
// })
const dic = {
  ar: {
    names: {
      email: 'البريد الاليكتروني',
      phone: 'رقم الهاتف',
      Password: 'كلمة السر',
      'Password Confirm': 'تاكيد كلمة المرور',
      website: 'الموقع الالكتروني',
      country: 'المحافظة',
      landline_number: 'الرقم المباشر',
      direct_employee_name: 'اسم الموظف المباشر',
      direct_employee_phone: 'رقم هاتف الموظف المباشر',
      factory_register: 'سجل المصنع',
      factory_register_image: 'صورة سجل المصنع',
      letter_image: 'صورة الخطاب',
      address: 'العنوان',
    },
  },
}
localize('ar', dic.ar)
/* eslint guard-for-in: 0 */
/* eslint no-restricted-syntax: 0 */
export default {
  name: 'CreateContract',
  components: {
    ValidationObserver,
    ValidationProvider,
    FormWizard,
    Husband,
    TabContent,
    BRow,
    BCol,
    BFormGroup,
    BFormInput,
    BFormRadioGroup,
    BFormTextarea,
    // BFormSelect,
    // eslint-disable-next-line vue/no-unused-components
    ToastificationContent,
    Attachments,
    Wife,
    Agent,
  },
  data() {
    return {
      husbandFront: null,
      husbandBack: null,
      wifeFront: null,
      wifeBack: null,
      firstWitnessFront: null,
      firstWitnessBack: null,
      secondWitnessFront: null,
      secondWitnessBack: null,
      agentFront: null,
      agentBack: null,
      divorceCertificate: null,
      husbandDeathCertificate: null,

      quranDate: null,
      isMosque: 1,
      quranAddress: null,
      selected: 'first',
      options: [
        { text: 'في المسجد', value: 1 },
        { text: 'خارج المسجد', value: 0 },
      ],
      array: [],
      maritalOptionHusband: [
        {
          id: 'single',
          name: 'اعزب',
        },
        {
          id: 'divorced',
          name: 'مطلق',
        },
        {
          id: 'married',
          name: 'متزوج',
        },
        {
          id: 'widower',
          name: 'ارمل',
        },
      ],
      maritalOptionWife: [
        {
          id: 'single',
          name: 'اعزب',
        },
        {
          id: 'divorced',
          name: 'مطلق',
        },
        {
          id: 'widower',
          name: 'ارمل',
        },
      ],
      husband: {
        firstName: '',
        middleName: '',
        lastName: '',
        email: '',
        birthdate: '2020-01-01',
        birthplace: '',
        maritalStatus: 'single',
        nationality: '',
        profession: '',
        address: '',
        phone: '',
        type: 'husband',
      },
      wife: {
        firstName: '',
        middleName: '',
        lastName: '',
        email: '',
        birthdate: '22-22-2035',
        birthplace: '',
        maritalStatus: 'single',
        nationality: '',
        profession: '',
        address: '',
        phone: '',
        type: 'wife',
      },
      firstWitness: {
        firstName: '',
        middleName: '',
        lastName: '',
        email: '',
        birthdate: '22-22-2035',
        birthplace: '',
        maritalStatus: 'single',
        nationality: '',
        profession: '',
        address: '',
        phone: '',
        type: 'first_witness',
      },
      secondWitness: {
        firstName: '',
        middleName: '',
        lastName: '',
        email: '',
        birthdate: '22-22-2035',
        birthplace: '',
        maritalStatus: 'single',
        nationality: '',
        profession: '',
        address: '',
        phone: '',
        type: 'second_witness',
      },
      agent: {
        firstName: '',
        middleName: '',
        lastName: '',
        email: '',
        birthdate: '22-22-2035',
        birthplace: '',
        maritalStatus: 'single',
        nationality: '',
        profession: '',
        address: '',
        phone: '',
        type: 'agent',
        kinshipDegree: 'agent',
        kinshipDegreeOption: [
          {
            id: 'father',
            name: 'اب',
          },
          {
            id: 'brother',
            name: 'اخ',
          },
          {
            id: 'uncle',
            name: 'عم',
          },
          {
            id: 'grand_father',
            name: 'جد',
          },
        ],
      },
      required,
      // ? Default locale for VeeValidate is 'en'
      locale: 'ar',
    }
  },
  created() {
    // this.$store.dispatch('contract/show', this.$route.params.id).then(res => {
    //   this.fetchData(res.data.data)
    // })
  },
  methods: {
    statusFile(status) {
      let data = null
      switch (status) {
        case 'single':
          data = 's'
          break
        case 'divorced':
          data = 'd'
          break
        case 'widower':
          data = 'w'
          break
        default:
          data = 's'
      }
      return data
    },
    prepareData() {
      this.array = []
      this.array.push(this.husband)
      this.array.push(this.wife)
      this.array.push(this.firstWitness)
      this.array.push(this.secondWitness)
      this.array.push(this.agent)
    },
    formSubmitted() {
      this.prepareData()
      const formData = new FormData()

      formData.append('husbandFront', this.husbandFront)
      formData.append('husbandBack', this.husbandBack)
      formData.append('wifeFront', this.wifeFront)
      formData.append('wifeBack', this.wifeBack)
      formData.append('firstWitnessFront', this.firstWitnessFront)
      formData.append('firstWitnessBack', this.firstWitnessBack)
      formData.append('secondWitnessFront', this.secondWitnessFront)
      formData.append('secondWitnessBack', this.secondWitnessBack)
      formData.append('agentFront', this.agentFront)
      formData.append('agentBack', this.agentBack)
      if (this.divorceCertificate) {
        formData.append('divorceCertificate', this.divorceCertificate)
      }
      if (this.husbandDeathCertificate) {
        formData.append('husbandDeathCertificate', this.husbandDeathCertificate)
      }

      formData.append('quran_date', this.quranDate)
      formData.append('is_mosque', this.isMosque)
      formData.append('quran_address', this.quranAddress)

      // Object.keys(this.contract).forEach(key => formData.append(key, this.contract[key]))
      this.array.forEach((value, index) => {
        formData.append(`data[${index}][first_name]`, value.firstName)
        formData.append(`data[${index}][middle_name]`, value.middleName)
        formData.append(`data[${index}][last_name]`, value.lastName)
        formData.append(`data[${index}][email]`, value.email)
        formData.append(`data[${index}][birth_date]`, value.birthdate)
        formData.append(`data[${index}][birth_place]`, value.birthplace)
        formData.append(`data[${index}][marital_status]`, value.maritalStatus)
        formData.append(`data[${index}][nationality]`, value.nationality)
        formData.append(`data[${index}][profession]`, value.profession)
        formData.append(`data[${index}][address]`, value.address)
        formData.append(`data[${index}][phone_number]`, value.phone)
        formData.append(`data[${index}][type]`, value.type)
        formData.append(`data[${index}][kinship_degree]`, 'father')
      })
      this.$store.dispatch('contract/store', formData).then(() => {
        this.$router.push({ name: 'contracts' })
        this.$toast({
          component: ToastificationContent,
          props: {
            title: 'تم اضافة العقد بنجاح',
            icon: 'EditIcon',
            variant: 'success',
          },
        })
      })
    },
    formatDate(datetime) {
      const array = datetime.split(' ')
      return array[0]
    },
    getDate() {
      const days = 1
      return new Date(Date.now() + days * 24 * 60 * 60 * 1000)
        .toISOString().split('T')[0]
    },
    validationFormDate() {
      return new Promise((resolve, reject) => {
        this.$refs.infoDateRules.validate().then(success => {
          if (success) {
            const role = JSON.parse(localStorage.getItem('info')).role.name
            if (role === 'user') {
              this.$http.get('/user/check').then(res => {
                if (res.data.data === 'true') {
                  this.dangerMessage(this.$t('message.createdContractBefore'))
                  reject()
                } else {
                  resolve(true)
                }
              })
            } else {
              resolve(true)
            }
          } else {
            reject()
          }
        })
      })
    },
    validationForm() {
      return new Promise((resolve, reject) => {
        this.$refs.husbandRules.validate().then(success => {
          if (success) {
            resolve(true)
          } else {
            reject()
          }
        })
      })
    },
    validationFormWife() {
      return new Promise((resolve, reject) => {
        this.$refs.wifeRules.validate().then(success => {
          if (success) {
            resolve(true)
          } else {
            reject()
          }
        })
      })
    },
    validationFormFirstWitness() {
      return new Promise((resolve, reject) => {
        this.$refs.firstWitnessRules.validate().then(success => {
          if (success) {
            resolve(true)
          } else {
            reject()
          }
        })
      })
    },
    validationFormSecondWitness() {
      return new Promise((resolve, reject) => {
        this.$refs.secondWitnessRules.validate().then(success => {
          if (success) {
            resolve(true)
          } else {
            reject()
          }
        })
      })
    },
    validationFormAgent() {
      return new Promise((resolve, reject) => {
        this.$refs.agentRules.validate().then(success => {
          if (success) {
            resolve(true)
          } else {
            reject()
          }
        })
      })
    },
    dangerMessage(message) {
      this.$toast({
        component: ToastificationContent,
        props: {
          title: message,
          icon: 'EditIcon',
          variant: 'danger',
        },
      })
    },
  },
}
</script>
<style scoped></style>
